// FOOTER
footer {
  width: 339px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  grid-row-start: 5;
  align-self: center;
  a {
    font-size: 1rem;
    color: $black;
    text-decoration: none;
    &:hover {
      color: $blue; }

    .fa-facebook-f {
      &:hover {
        color: #3b5998; } }
    .fa-twitter {
      &:hover {
        color: #00acee; } }
    .fa-youtube {
      &:hover {
        color: #c4302b; } } }

  .footerPodijeliSocial {
    font-size: 1.5rem; }

  @media screen and (max-width: 800px) {
    width: auto;
    flex-direction: column;
    .footerDonacija {
      margin-bottom: 1rem; } } }
